import React from 'react';
import axios from 'axios';
import Movie from '../component/Movie';
import './Home.css';

class Home extends React.Component {
    state = {
        isLoading: true,
        movies: [],
    };
    getMovies = async () => {
        const {
            data: {
                data: { movies },
            },
        } = await axios.get('https://yts.mx/api/v2/list_movies.json?sort_by=rating');
        this.setState({ movies, isLoading: false });
    };

    componentDidMount() {
        this.getMovies();
    }

    render() {
        const { isLoading, movies } = this.state;  // 구조분해 할당으로 this.state.에 있는 isLoading을 우선 얻으면 항상 this.state를 입력하지 않아도 된다.
        return (
            <section className="container">
                {isLoading ? (
                    <div className="loader">
                        <span className="loader__text">Loading...</span>
                    </div>
                ) : (
                        <div className="movies">
                            {movies.map(movie => (
                                <Movie
                                    key={movie.id} // 여기에 key props 를 추가
                                    id={movie.id}
                                    year={movie.year}
                                    title={movie.title}
                                    summary={movie.summary}
                                    poster={movie.medium_cover_image}
                                    genres={movie.genres}
                                />
                            ))}
                        </div>
                    )}
            </section>
        );
    }
}

export default Home;
